<template>
  <div class="Capitulo1">
	<h1>DAS DISPOSIES GERAIS</h1>
		
		<p><strong>LEI N 16.642/17</strong></p>
		<p>Aprova o Cdigo de Obras e Edificaes do Municpio de So Paulo; introduz alteraes nas Leis n 15.150, de 6 de maio de 2010, e n 15.764, de 27 de maio de 2013.</p>
		<p><strong>JOO DORIA</strong>, Prefeito do Municpio de So Paulo, no uso das atribuies que lhe so conferidas por lei, faz saber que a Cmara Municipal, em sesso de 1 de julho de 2016, decretou e eu promulgo a seguinte lei:</p>
		<div class="decreto">
			<p><strong>DECRETO N 57.776/17</strong>*</p>
			<p>Regulamenta a Lei n 16.642, de 9 de maio de 2017, que aprovou o Cdigo de Obras e Edificaes do Municpio de So Paulo; define os membros da Comisso de Edificaes e Uso do Solo  CEUSO.</p>
			<p><strong>JOO DORIA</strong>, Prefeito do Municpio de So Paulo, usando das atribuies que lhe so conferidas por lei, e CONSIDERANDO as disposies dos artigos 122 e 123 do Cdigo de Obras e Edificaes do Municpio de So Paulo, DECRETA:</p>
			<p align="right">* Com as retificaes do Decreto n 57.815/17 consideradas.</p>
		</div>

		<hr />

		<p><strong>Art. 1</strong> Fica aprovado o Cdigo de Obras e Edificaes do Municpio de So Paulo  COE, que disciplina, no Municpio de So Paulo, as regras gerais a serem observadas no projeto, no licenciamento, na execuo, na manuteno e na utilizao de obras, edificaes e equipamentos, dentro dos limites do imvel, bem como os respectivos procedimentos administrativos, executivos e fiscalizatrios, sem prejuzo do disposto na legislao estadual e federal pertinente.</p>
		<div class="decreto">
			<strong class="artigo-decreto">Art. 1 </strong>Este decreto regulamenta os procedimentos administrativos e executivos e fixa as regras gerais e especficas a serem obedecidas no projeto, licenciamento, execuo, manuteno, utilizao e fiscalizao de obras, edificaes, equipamentos, obras complementares, terraos e mobilirios, dentro dos limites dos imveis em que se situam, nos termos do disposto na Lei n. 16.642, de 9 de maio de 2017, que aprovou o Cdigo de Obras e Edificaes do Municpio de So Paulo  COE.</p>
			<p><strong> 1 </strong>Integram este decreto os anexos a seguir discriminados</p>
			<p><strong>I -</strong> Anexo I  Disposies Tcnicas;</p>
			<p><strong>II -</strong> Anexo II  Desenhos;</p>
			<p><strong>III -</strong> Anexo III  Tabela de Multas;</p>
			<p><strong>IV - </strong>Anexo IV  Tabelas Relativas s reas No Computveis.</p>
			<p><strong> 2 </strong>Os modelos de projeto simplificado, quadros, legendas, documentos e declaraes para instruir cada um dos pedidos de emisso de documentos de atividade edilcia de que trata o COE sero fixados mediante portaria da Secretaria Municipal de Urbanismo e Licenciamento  SMUL.</p>
			<strong>Art. 2 </strong>Para os fins deste decreto, os rgos, entidades e expresses sero identificados pelas seguintes siglas ou abreviaturas</p>
			<p><strong>I - </strong>ART  Anotao de Responsabilidade Tcnica perante o CREA;</p>
			<p><strong>II - </strong>AVCB  Auto de Vistoria do Corpo de Bombeiros;</p>
			<p><strong>III - </strong>CAU  Conselho de Arquitetura e Urbanismo;</p>
			<p><strong>IV - </strong>CCIR  Certificado de Cadastro de Imvel Rural;</p>
			<p><strong>V - </strong>CEPAC  Certificado de Potencial Adicional Construtivo;</p>
			<p><strong>VI - </strong>COE  Cdigo de Obras e Edificaes;</p>
			<p><strong>VII - </strong>CREA Conselho Regional de Engenharia e Agronomia;</p>
			<p><strong>VIII - </strong>DIS  Decreto de Declarao de Interesse Social;</p>
			<p><strong>IX - </strong>DOC  Dirio Oficial da Cidade;</p>
			<p><strong>X - </strong>DUP  Decreto de Declarao de Utilidade Pblica;</p>
			<p><strong>XI - </strong>ISS  Imposto Sobre Servios de Qualquer Natureza;</p>
			<p><strong>XII - </strong>IEOS  Intimao para Execuo de Obras e Servios;</p>
			<p><strong>XIII - </strong>IT  Instruo Tcnica (Corpo de Bombeiros);</p>
			<p><strong>XIV - </strong>LOE  Legislao de Obras e Edificaes, incluindo o COE;</p>
			<p><strong>XV - </strong>LPUOS  Legislao de Parcelamento, Uso e Ocupao do Solo;</p>
			<p><strong>XVI - </strong>NEC  Notificao de Exigncias Complementares;</p>
			<p><strong>XVII - </strong>NT  Norma Tcnica, expedida por rgo competente, exceto ABNT;</p>
			<p><strong>XVIII - </strong>NTO  Norma Tcnica Oficial (registrada na Associao Brasileira de Normas Tcnicas  ABNT);</p>
			<p><strong>XIX - </strong>OU  Operao Urbana;</p>
			<p><strong>XX - </strong>OUC  Operao Urbana Consorciada;</p>
			<p><strong>XXI - </strong>PDE  Plano Diretor Estratgico;</p>
			<p><strong>XXII - </strong>PR  Prefeitura Regional</p>
			<p><strong>XXIII - </strong>Prefeitura  Prefeitura do Municpio de So Paulo;</p>
			<p><strong>XXIV - </strong>RRT  Registro de Responsabilidade Tcnica perante o CAU;</p>
			<p><strong>XXV - </strong>SLCe  Sistema de Licenciamento Eletrnico de Construo;</p>
			<p><strong>XXVI - </strong>SMPR  Secretaria Municipal das Prefeituras Regionais;</p>
			<p><strong>XXVII - </strong>SMUL  Secretaria Municipal de Urbanismo e Licenciamento;</p>
			<p><strong>XXVIII - </strong>TEV/COE  Taxa para Exame e Verificao dos Pedidos de Documentos de Controle da Atividade Edilcia.</p>
			<strong>Art. 3 </strong>Enquadram-se como processos especiais, com procedimento definido neste decreto, os pedidos de emisso dos seguintes documentos de controle da atividade edilcia previstos no artigo 12 do COE:</p>
			<p><strong>I - </strong>Alvar de Aprovao;</p>
			<p><strong>II - </strong>Alvar de Execuo;</p>
			<p><strong>III - </strong>Projeto Modificativo;</p>
			<p><strong>IV - </strong>Certificado de Concluso;</p>
			<p><strong>V - </strong>Certificado de Regularizao;</p>
			<p><strong>VI - </strong>Certificado de Acessibilidade;</p>
			<p><strong>VII - </strong>Certificado de Segurana;</p>
			<p><strong>VIII - </strong>Alvar de Autorizao;</p>
			<p><strong>IX - </strong>Cadastro de Equipamento;</p>
			<p><strong>X - </strong>Manuteno de Equipamento;</p>
			<p><strong>XI - </strong>Ficha Tcnica;</p>
			<p><strong>XII - </strong>Diretrizes de Projeto.</p>
			<strong>Art. 4 </strong>Compete  Secretaria Municipal de Urbanismo e Licenciamento a anlise e deciso dos pedidos de documentos de controle da atividade edilcia de que tratam o COE e este decreto.</p>
			<p> 1 </strong>A competncia de anlise e deciso poder ser transferida s PRs e SMPR por decreto, em razo da metragem da rea, da lotao ou da categoria de uso.</p>
			<p> 2 </strong>Ficam mantidas as delegaes de competncias s PRs e SMPR, at a reviso dos respectivos decretos de delegao de competncia em vigor.</p>
		</div>

		<p><strong>Art. 2 </strong>A anlise dos projetos e dos pedidos de documentos de controle da atividade edilcia deve ser efetuada quanto  sua observncia:</p>

		<p><strong>I - </strong>s normas do <strong>Plano Diretor Estratgico  PDE</strong><sup>1</sup>, da <strong>Legislao de Parcelamento, Uso e Ocupao do Solo  LPUOS</strong><sup>2</sup> e das <strong>Operaes Urbanas Consorciadas  OUC</strong><sup>3</sup>;</p>

		<div class="glossario-box">
			<p><span class="nota">1</span> Plano Diretor Estratgico  PDE (Lei n 16.050, de 31 de julho de 2014), dispe sobre a Poltica de Desenvolvimento Urbano, o Sistema de Planejamento Urbano e o Plano Diretor Estratgico do Municpio de So Paulo e aplica-se  totalidade do seu territrio.</p>
			<p><span class="nota">2</span> Lei de Parcelamento, Uso e Ocupao do Solo  LPUOS (Lei n 16.402, de 22 de maro de 2016) Dispe sobre o parcelamento, o uso e a ocupao do solo no territrio do Municpio de So Paulo de acordo com o Plano Diretor Estratgico  PDE, e legislao correlata.</p>
			<p><span class="nota">3</span> Operao Urbana / Operao Urbana Consorciada  OU / OUC so instrumentos cujas diretrizes so previstas pelo PDE e aprovadas mediante lei municipal, realizadas sob a coordenao do Poder Pblico, envolvendo a iniciativa privada, empresas prestadoras de servios pblicos, moradores e usurios do local, em permetro delimitado do territrio a fim de conferir melhorias urbansticas que atendam s demandas sociais, ambientais e econmicas existentes.</p>
		</div>

		
		<p><strong>II - </strong>aos planos de melhoramento virio aprovados;</p>
		<p><strong>III - </strong>s servides administrativas;</p>
		<p><strong>IV - </strong>s restries decorrentes das declaraes de utilidade pblica e de interesse social;</p>
		<p><strong>V - </strong>s limitaes decorrentes do tombamento e da preservao de imveis;</p>
		<p><strong>VI - </strong>s regras para mitigar o impacto ambiental e de vizinhana;</p>
		<p><strong>VII - </strong>s restries para a ocupao de reas com risco ou contaminadas;</p>
		<p><strong>VIII - </strong>a quaisquer leis ou regulamentos relacionados s caractersticas externas da edificao ou equipamento e sua insero na paisagem urbana;</p>
		<p><strong>IX - </strong>s exigncias relativas s condies de segurana de uso das edificaes com alto potencial de risco de incndios e situaes de emergncia.</p>
		<p><strong class="artigo-lei">Art. 3 </strong>Para fins de aplicao das disposies deste Cdigo ficam adotadas as seguintes definies:</p>
		<p><strong>I - </strong>acessibilidade: condio de utilizao, com segurana e autonomia, total ou assistida, por pessoa com deficincia ou com mobilidade reduzida, de edificao, espao, mobilirio e equipamento;</p>
		<p><strong>II - </strong>acessvel: edificao, espao, mobilirio e equipamento que possa ser utilizado e vivenciado por qualquer pessoa, inclusive aquela com deficincia ou mobilidade reduzida, conforme os parmetros definidos em norma tcnica pertinente;</p>
		<p><strong>III - </strong>adaptao razovel: modificaes e ajustes necessrios e adequados que no acarretem nus desproporcional ou indevido, quando requeridos em cada caso, a fim de assegurar que as pessoas com deficincia ou mobilidade reduzida possam gozar ou exercer, em igualdade de oportunidades com as demais pessoas, os direitos humanos e liberdades fundamentais;</p>
		<p><strong>IV - </strong>alinhamento: linha de divisa entre o terreno e o logradouro pblico;<strong></strong></p>
		<p><strong>V - </strong>andar: volume compreendido entre dois pavimentos consecutivos ou entre o pavimento e o nvel superior de sua cobertura;</p>
		<p><strong class="bolinha">VI - </strong><a class="ref_ilust" href="#ilust">tico</a>: parte do volume superior de uma edificao, destinada a abrigar casa de mquinas, piso tcnico de elevadores, equipamentos, caixa dgua e circulao vertical;</p>
		<p><strong class="bolinha">VII - </strong><a class="ref_ilust" href="#ilust">beiral</a>: prolongamento da cobertura que se sobressai das paredes externas da edificao;</p>
		<p><strong>VIII - </strong>canteiro de obras: espao delimitado pelo tapume, destinado ao preparo e apoio  execuo da obra ou servio, incluindo os elementos provisrios que o compem, tais como estande de vendas, alojamento, escritrio de campo, depsitos, galeria, andaime, plataforma e tela protetora visando  proteo da edificao vizinha e logradouro pblico;</p>
		<p><strong>IX - </strong>demolio: total derrubamento de uma edificao;</p>
		<p><strong>X - </strong>edificao: obra coberta destinada a abrigar atividade humana ou qualquer instalao, equipamento e material;</p>
		<p><strong>XI - </strong>edificao transitria: edificao de carter no permanente, passvel de montagem, desmontagem e transporte;</p>
		<p><strong>XII - </strong>embargo: ordem de paralisao dos trabalhos na obra ou servio em execuo sem a respectiva licena ou por desatendimento  Legislao de Obras e Edificaes  LOE ou LPUOS;</p>
		<p><strong>XIII - </strong>equipamento: elemento no considerado como rea construda, destinado a guarnecer ou completar uma edificao, a ela se integrando, tais como equipamentos mecnicos de transporte, tanques de armazenagem, bombas e sistemas de energia, aquecimento solar e a gs, podendo ser:</p>
		<p><strong>a)</strong> equipamento permanente: equipamento de carter duradouro;</p>
		<p><strong>b)</strong> equipamento transitrio: equipamento de carter no permanente, passvel de montagem, desmontagem e transporte;</p>
		<p><strong>XIV - </strong>interdio: ordem e ato de fechamento e desocupao do imvel em situao irregular ou de risco em relao s condies de estabilidade, segurana ou salubridade;</p>
		<p><strong>XV - </strong>mobilirio: elemento construtivo que no se enquadra como edificao ou equipamento, tais como:</p>
		<p><strong>a)</strong> guarita e mdulo pr-fabricado;</p>
		<p><strong class="bolinha">b)</strong> <a class="ref_ilust" href="#ilust">jirau</a>, elemento constitudo de estrado ou passadio, instalado a meia altura em compartimento;</p>
		<p><strong>c) </strong>abrigo ou telheiro sem vedao lateral em pelo menos 50% (cinquenta por cento) do permetro;</p>
		<p><strong>d) </strong>estufas, quiosques, viveiros de plantas, churrasqueiras;</p>
		<p><strong>e) </strong>dutos de lareiras;</p>
		<p><strong class="bolinha">f) </strong><a class="ref_ilust" href="#ilust">prgulas</a>;</p>
		<p><strong>XVI - </strong>movimento de terra: modificao do perfil do terreno ou substituio do solo em terrenos alagadios ou que implique em alterao topogrfica superior a 1,50 m (um metro e cinquenta centmetros) de desnvel ou a 1.000,00 m (mil metros cbicos) de volume;</p>
		<p><strong>XVII - </strong>muro de arrimo: muro resistente, que trabalha por gravidade ou flexo, construdo para conter macio de terra, empuxo das guas de infiltrao, sobrecarga de construo, sobreaterro e situaes similares;</p>
		<p><strong class="bolinha">XVIII - </strong><a class="ref_ilust" href="#ilust">obra complementar</a>: edificao secundria ou parte da edificao que, funcionalmente, complemente a atividade desenvolvida no imvel, tais como:</p>
		<p><strong>a)</strong> passagem coberta de pedestre sem vedao lateral;</p>
		<p><strong>b)</strong> abrigo de porta e porto, automvel, lixo, recipiente de gs e entrada e medidores de concessionrias;</p>
		<p><strong>c)</strong> casa de mquina isolada, cabine de fora, cabine primria;</p>
		<p><strong>d)</strong> reservatrio em geral, elevado e enterrado, chamin e torre isoladas;</p>
		<p><strong>e) </strong>bilheteria, portaria, caixa eletrnico;</p>
		<p><strong>XIX - </strong>obras de emergncia: obras de carter urgente, essenciais  garantia das condies de estabilidade, segurana ou salubridade do imvel;</p>
		<p><strong>XX - </strong>pavimento: plano de piso;</p>
		<p><strong>XXI - </strong>pavimento trreo: aquele definido na LPUOS;</p>
		<p><strong>XXII - </strong>pavimento de acesso: aquele definido na LPUOS;</p>
		<p><strong>XXIII - </strong>pea grfica: representao grfica de elementos para a compreenso de um projeto ou obra;</p>
		<p><strong>XXIV - </strong>prgulas: vigas horizontais ou inclinadas, sem cobertura;</p>
		<p><strong>XXV - </strong>pessoa com deficincia: aquela que tem impedimento de longo prazo de natureza fsica, mental, intelectual ou sensorial, o qual, em interao com uma ou mais barreiras, pode obstruir sua participao plena e efetiva na sociedade em igualdade de condies com as demais pessoas;</p>
		<p><strong>XXVI - </strong>pessoa com mobilidade reduzida: aquela que tenha, por qualquer motivo, dificuldade de movimentao, permanente ou temporria, gerando reduo efetiva da mobilidade, da flexibilidade, da coordenao motora ou da percepo, incluindo idoso, gestante, lactante, pessoa com criana de colo e obeso;</p>
		<p><strong>XXVII - </strong>reconstruo: obra destinada  recuperao e recomposio de uma edificao, motivada pela ocorrncia de incndio ou outro sinistro, mantendo-se as caractersticas anteriores, observadas as condies de adaptao  segurana de uso e de acessibilidade estabelecidas nesta lei;</p>
		<p><strong class="bolinha">XXVIII - </strong><a class="ref_ilust" href="#ilust">recuo</a>: distncia entre o limite externo da edificao e a divisa do lote, medida perpendicularmente a esta;</p>
		<p><strong>XXIX - </strong>reforma: interveno na edificao que implique alterao da rea construda ou da volumetria, com a simultnea manuteno de parte ou de toda a rea existente, com ou sem mudana de uso;</p>
		<p><strong>XXX - </strong>reforma sem acrscimo de rea: interveno na edificao sem alterao da rea construda, que implique em modificao da estrutura, p-direito ou compartimentao vertical, com ou sem mudana de uso;</p>
		<p><strong>XXXI - </strong>reparo: obra ou servio destinado  manuteno de um edifcio, sem implicar mudana de uso, acrscimo ou supresso de rea, alterao da estrutura, da compartimentao horizontal e vertical, da volumetria, e dos espaos destinados  circulao, iluminao e ventilao;</p>
		<p><strong>XXXII - </strong>requalificao: interveno em edificao existente, visando  adequao e modernizao das instalaes, com ou sem mudana de uso;</p>
		<p><strong>XXXIII - </strong>salincia: elemento arquitetnico, engastado ou aposto na edificao ou muro, tais como aba horizontal e vertical, marquise, jardineira, floreira, ornamento e brise;</p>
		<p><strong class="bolinha">XXXIV - </strong><a class="ref_ilust" href="#ilust">tapume</a>: vedao provisria usada durante a construo, visando  proteo de terceiros e ao isolamento da obra ou servio;</p>
		<p><strong class="bolinha">XXXV - </strong><a class="ref_ilust" href="#ilust">terrao aberto</a>: pea justaposta  edificao, constituda em balco aberto, sem ou com vedao, desde que retrtil ou vazada do tipo quebra-sol, em balano ou no, complementar  unidade residencial ou no residencial, no abrigando funo essencial ao pleno funcionamento da unidade;</p>

		<p><strong>XXXVI - </strong>uso privado: espao ou compartimento de utilizao exclusiva da populao permanente da edificao;</p>
		<p><strong>XXXVII - </strong>uso restrito: espao, compartimento, ou elemento interno ou externo, disponvel estritamente para pessoas autorizadas.</p>
		<p><strong>Pargrafo nico. </strong>Ficam tambm adotadas as seguintes abreviaes:</p>
		<p><strong>I - </strong>ART  Anotao de Responsabilidade Tcnica perante o CREA;</p>
		<p><strong>II - </strong>CAU  Conselho de Arquitetura e Urbanismo;</p>
		<p><strong>III - </strong>COE  Cdigo de Obras e Edificaes;</p>
		<p><strong>IV - </strong>CREA  Conselho Regional de Engenharia e Agronomia;</p>
		<p><strong>V - </strong>LOE  Legislao de Obras e Edificaes, incluindo o COE;</p>
		<p><strong>VI - </strong>LPUOS  Legislao de Parcelamento, Uso e Ocupao do Solo;</p>
		<p><strong>VII - </strong>PDE  Plano Diretor Estratgico;</p>
		<p><strong>VIII - </strong>Prefeitura  Prefeitura do Municpio de So Paulo;</p>
		<p><strong>IX - </strong>RRT  Registro de Responsabilidade Tcnica perante o CAU.</p>

		<div class="decreto">
			<strong>Art. 5 </strong>Para fins de aplicao das disposies do COE e deste Cdigo ficam adotadas, alm das dispostas em lei, as seguintes definies:</p>
			<strong>I - </strong>baixa de responsabilidade tcnica: a cincia, por parte da Prefeitura, do desligamento do profissional responsvel pela execuo da obra regularmente licenciada, autorizada ou comunicada;</p>
			<strong>II - </strong>assuno de responsabilidade tcnica: a aceitao, por parte da Prefeitura, de novo profissional, legalmente habilitado, que passa a responder pela direo tcnica relativa  execuo de obra regularmente licenciada, autorizada ou comunicada;</p>
			<strong>III - </strong>transferncia de responsabilidade tcnica: a ocorrncia simultnea de baixa e assuno de responsabilidade tcnica.</p>
			<strong>IV - </strong>demolio: total ou parcial derrubamento da edificao existente.</p>
		</div>

		<hr id="ilust" />

