<template>
  <div class="Apresentacao">
	<h2>Simplificando o Licenciamento em So Paulo</h2>
	<p>O Cdigo de Obras e Edificaes  COE, sancionado pelo Prefeito Joo Dria em maio de 2017 e em vigor desde julho de 2017, apresenta um novo modelo de licenciamento de edificaes. O COE vem para resolver entraves que perduram por anos e dificultam a transformao de So Paulo em sua vocao de cidade dinmica e global.</p>
	<p>A primeira conquista deste Cdigo  a simplificao. Com base na experincia administrativa da SMUL em liberar edificaes, o texto da lei tambm detalhou procedimentos internos para facilitar e desburocratizar a anlise dos projetos pelos tcnicos. A nova lei est menos burocrtica e em consonncia com as diretrizes do Plano Diretor Estratgico e da Lei de Parcelamento, Uso e Ocupao do Solo.  Visa, assim, deixar os procedimentos e o processo de licenciamento mais simples, tanto para o cidado, quanto para os tcnicos, alm de contribuir para o crescimento e desenvolvimento da cidade.</p>
	<p>Com este COE, os detalhes internos das edificaes ficam a critrio do proprietrio e do autor do projeto, que agora possuem responsabilidades explcitas. Deste modo, a Prefeitura poder se focar nos aspectos urbansticos, ambientais, de sustentabilidade, acessibilidade e segurana dos empreendimentos.</p>
	<p>Hoje, na administrao pblica, perseguimos o princpio da transparncia. Com os regramentos administrativos mais claros, existem poucas exigncias tcnicas complementares, o que d agilidade ao processo. Em relao aos cdigos anteriores, este no legisla quando j existem normas tcnicas, evitando sobreposies de normas, alm de possibilitar que o profissional habilitado exera seu oficio com responsabilidade.</p>
	<p>Este COE traz avanos significativos. Foram revistos os prazos, indeferimentos, instncias, alm de outros assuntos importantes que tornaram a legislao edilcia mais moderna. Agora h poucas regras edilcias e urbansticas efetivamente relevantes. A racionalizao e agilizao; a responsabilizao de empreendedores e profissionais; a modernizao e informatizao dos procedimentos so os principais destaques a lei.</p>
	<p>Com o novo COE, a Secretaria Municipal de Urbanismo e Licenciamento est mais perto de atingir sua meta de modernizar o licenciamento na cidade de So Paulo. A expectativa  de diminuir consideravelmente o tempo de licenciamento para os empreendimentos na cidade, tornar os processos eletrnicos, e assim, ter projetos com maior qualidade no Municpio.</p>
	<p style="text-align: right; margin-top: 2em;"><strong>Heloisa Maria de Salles Penteado Proena</strong><br /> 
	Secretria Municipal de Urbanismo e Licenciamento</p>

	<h2>Apresentao</h2>
	<h3>O que  um Cdigo de Obras?</h3>
	<p>O Cdigo de Obras e Edificaes (COE)  o instrumento que disciplina as regras gerais de licenciamento da atividade edilcia, bem como de fiscalizao da execuo, manuteno e utilizao de obras e equipamentos. Alm disso, estabelece normas edilcias especficas a serem observadas no Municpio de So Paulo, em complemento s fixadas por Normas Tcnicas relacionadas  matria.</p>
	<p>O COE do Municpio de So Paulo, aprovado e sancionado em 09 de maio de 2017, vem para completar o Marco Regulatrio Municipal, ao lado do Plano Diretor Estratgico (PDE) e da Lei de Parcelamento, Uso e Ocupao do Solo (LPUOS), revisados recentemente. </p>
	<p>No novo COE os componentes prioritrios de avaliao so urbansticos, ambientais e de vizinhana. Asseguram que a Prefeitura mantenha seu compromisso em estabelecer instrumentos de planejamento urbano, capazes de organizar melhor os espaos da cidade de modo a torn-la um lugar mais agradvel para se viver.</p>
	
	<h3>Como foi o processo de reviso?</h3>
	<p>Considerando que o cdigo anterior encontra-se em vigor h 25 anos e com base na experincia prtica da sua aplicao, a Prefeitura de So Paulo iniciou um processo de avaliao e atualizao dessas normas, tendo em vista oferecer resposta a novas demandas relacionadas  produo e consumo do ambiente construdo, bem como adequao ao marco atual da disciplina urbanstica, inaugurado com a aprovao do novo Plano Diretor Estratgico  PDE.</p>
	<p>A primeira providncia desse trabalho foi a elaborao e consolidao de todas as alteraes e incluses promovidas no Cdigo de Obras e Edificaes, desde junho de 1992 at o presente.</p>

	<p>A partir do texto compilado, iniciou-se o processo de reviso do COE no mbito da <strong>Secretaria Municipal de Licenciamento  SEL</strong><sup class="referencias-notas">1</sup>, atual Secretaria Municipal de Urbanismo e Licenciamento  SMUL, envolvendo representantes da Secretaria Executiva dos rgos Colegiados (SEC), Coordenadorias de Edificao Residencial (RESID), Edificao de Servios e Uso institucional (SERVIN), Edificao de Uso Comercial e Industrial (COMIN), Parcelamento do Solo e Habitao de Interesse Social (PARHIS), Atividade Especial e Segurana de Uso (SEGUR) e Gabinete da Secretaria.</p>
	
	<div class="glossario-box">
		<span class="nota">1</span> Conforme Decreto N 57.576, a Secretaria Municipal de Licenciamento (SEL) teve suas atribuies fundidas com a Secretaria Municipal de Desenvolvimento Urbano (SMDU), tornando-se em janeiro de 2017 a atual Secretaria Municipal de Urbanismo e Licenciamento (SMUL).</p>
		O mesmo decreto alterou as seguintes denominaes:</p>
		<ul>
			<li>Secretaria Municipal das Subprefeituras para Secretaria Municipal das Prefeituras Regionais (SMPR);</li>
			<li>Secretaria Municipal dos Negcios Jurdicos para Secretaria Municipal de Justia (SMJ);</li>
			<li>Secretaria Municipal dos Transportes para Secretaria Municipal de Mobilidade e Transportes (SMT);</li>
			<li>Secretaria Municipal de Infraestrutura Urbana para Secretaria Municipal de Servios e Obras (SMSO);</li>
			<li>Secretaria Municipal de Finanas e Desenvolvimento Econmico para Secretaria Municipal da Fazenda (SF);</li>
			<li>Secretaria Municipal da Pessoa com Deficincia e Mobilidade Reduzida para Secretaria Municipal da Pessoa com Deficincia (SMPED)</li>
		</ul>
	</div>

	<p>Em paralelo, realizaram-se rodadas de discusso com entidades representativas de profissionais ligados  engenharia e arquitetura, do setor imobilirio e da construo civil, que agregam a maior parte do pblico que, necessariamente, utiliza o COE na elaborao dos projetos submetidos ao licenciamento, em especial: Associao Brasileira dos Escritrios de Arquitetura (ASBEA), Sindicato das Empresas de Compra, Venda, Locao e Administrao de Imveis Residenciais e Comerciais de So Paulo (SECOVI-SP) e Sindicato da Indstria da Construo Civil do Estado de So Paulo (SINDUSCON-SP).</p>
	<p>Em novembro de 2013 foi apresentado ao Sr. Prefeito um plano de trabalho contendo os princpios orientadores do novo Cdigo: <strong>(1) Simplificao, (2) Clareza, (3) Estrutura da Lei, (4) Responsabilizao, (5) Atualizao, (6) Adequao ao novo Plano Diretor</strong>, conforme descrio abaixo.</p>

	<!-- apresentacao_d01.html -->
	
	<!-- Artboard: 01 -->
	<div id="g-00_apresentacao_d01-box" class="ai2html">
	<div id="g-00_apresentacao_d01-01" class="g-artboard g-artboard-v4" data-min-width='960'>
		<img id="g-00_apresentacao_d01-01-img" class="g-aiImg" :src="caminho + '00_apresentacao_d01-01.png'"/>
		<div id="g-ai0-1" class="g-img01 g-aiAbs g-aiPointText" style="top:4.503%;margin-top:-10.1px;left:70.767%;width:30px;">
			<p class="g-pstyle0">3</p>
		</div>
		<div id="g-ai0-2" class="g-img01 g-aiAbs g-aiPointText" style="top:4.503%;margin-top:-10.1px;left:3.7759%;width:30px;">
			<p class="g-pstyle0">1</p>
		</div>
		<div id="g-ai0-3" class="g-img01 g-aiAbs g-aiPointText" style="top:4.503%;margin-top:-10.1px;left:38.8921%;width:30px;">
			<p class="g-pstyle0">2</p>
		</div>
		<div id="g-ai0-4" class="g-img01 g-aiAbs g-aiPointText" style="top:8.6742%;margin-top:-10.5px;left:3.0725%;width:134px;">
			<p class="g-pstyle1">SIMPLIFICAO</span></p>
		</div>
		<div id="g-ai0-5" class="g-img01 g-aiAbs g-aiPointText" style="top:8.7533%;margin-top:-10.1px;left:37.9167%;width:90px;">
			<p class="g-pstyle1">CLAREZA</p>
		</div>
		<div id="g-ai0-6" class="g-img01 g-aiAbs g-aiPointText" style="top:8.7533%;margin-top:-10.1px;left:69.7917%;width:160px;">
			<p class="g-pstyle1">ESTRUTURA DA LEI</p>
		</div>
		<div id="g-ai0-7" class="g-img01 g-aiAbs" style="top:25.974%;left:37.9705%;width:25.7292%;">
			<p>O novo texto deve ter regras claras, permitindo objetividade na sua aplicao e reduzindo ao mximo a necessidade de interpretao, seja por tcnicos especializados ou por colegiados criados com essa nalidade.</p>
		</div>
		<div id="g-ai0-8" class="g-img01 g-aiAbs" style="top:25.974%;left:69.7783%;width:25.7292%;">
			<p>O Cdigo de Obras e Edicaes vem sendo utilizado por prossionais da rea h mais de 20 anos. Avaliou-se como desejvel manter a estrutura atual do texto, j conhecida e considerada adequada.</p>
		</div>
		<div id="g-ai0-9" class="g-img01 g-aiAbs" style="top:26.0921%;left:3.0725%;width:24.0625%;">
			<p>Considerando os limites da capacidade de controle do Poder Pblico sobre o processo de desenvolvimento urbano, optou-se por uma proposta restrita a regras edilcias e urbansticas efetivamente relevantes.</p>
		</div>
		<div id="g-ai0-10" class="g-img01 g-aiAbs g-aiPointText" style="top:49.9576%;margin-top:-10.1px;left:70.8097%;width:30px;">
			<p class="g-pstyle0">6</p>
		</div>
		<div id="g-ai0-11" class="g-img01 g-aiAbs g-aiPointText" style="top:49.9576%;margin-top:-10.1px;left:3.9962%;width:30px;">
			<p class="g-pstyle0">4</p>
		</div>
		<div id="g-ai0-12" class="g-img01 g-aiAbs g-aiPointText" style="top:49.9576%;margin-top:-10.1px;left:38.8921%;width:30px;">
			<p class="g-pstyle0">5</p>
		</div>
		<div id="g-ai0-13" class="g-img01 g-aiAbs g-aiPointText" style="top:54.1287%;margin-top:-10.5px;left:3.0725%;width:179px;">
			<p class="g-pstyle1">RESPONSABILIZAO</p>
		</div>
		<div id="g-ai0-14" class="g-img01 g-aiAbs g-aiPointText" style="top:54.1287%;margin-top:-10.5px;left:37.9705%;width:125px;">
			<p class="g-pstyle1">ATUALIZAO</p>
		</div>
		<div id="g-ai0-15" class="g-img01 g-aiAbs g-aiPointText" style="top:55.1204%;margin-top:-18.9px;left:69.8343%;width:206px;">
			<p class="g-pstyle1">ADEQUAO AO NOVO </p>
			<p class="g-pstyle1">PLANO DIRETOR E LPUOS</p>
		</div>
		<div id="g-ai0-16" class="g-img01 g-aiAbs" style="top:71.3105%;left:69.8298%;width:24.4792%;">
			<p>Considerando a nova disciplina urbanstica do municpio, coloca-se como necessria e oportuna a atualizao e adequao do COE aos novos marcos legais.</p>
		</div>
		<div id="g-ai0-17" class="g-img01 g-aiAbs" style="top:71.4286%;left:37.76%;width:25.7292%;">
			<p>O novo COE deve estar em consonncia com os processos de modernizao administrativa, voltados para a informatizao do licenciamento de obras, bem como atualizado em relao a novas tecnologias construtivas, demandas da sociedade e sustentabilidade ambiental. Deve ainda abrir mo de exigncias edilcias j regradas por Normas Tcnicas.</p>
		</div>
		<div id="g-ai0-18" class="g-img01 g-aiAbs" style="top:71.5466%;left:3.0725%;width:25.7292%;">
			<p>Alm de explicitar claramente as responsabilidades de proprietrio, possuidor, responsvel tcnico pelo projeto e responsvel tcnico pela obra, o contedo do novo COE deve reetir essa disposio, evitando excesso de exigncias quanto a detalhes dos projetos que no cabem  municipalidade aferir ou controlar.</p>
		</div>
	</div>
	</div>

	<p>Com base nessas premissas, o grupo tcnico formado junto  SEL elaborou uma primeira minuta que foi encaminhada  apreciao das demais secretarias envolvidas com o tema: Secretaria Municipal das Subprefeituras (SMSP), Secretaria Municipal de Desenvolvimento Urbano (SMDU), Secretaria Municipal dos Negcios Jurdicos (SNJ), Secreta ria Municipal dos Transportes (SMT), Secretaria Municipal do Verde e do Meio Ambiente (SVMA), Secretaria Municipal de Infra-estrutura Urbana (SIURB), Secretaria Municipal da Pessoa com Deficincia (SMPED), Secretaria Municipal de Finanas e Desenvolvimento Econmico (SF).</p>
	<p>Mediante sugestes via e-mail e aps reunies com cada Secretaria envolvida, a minuta foi aperfeioada e complementada. As contribuies que no foram incorporadas ao texto da lei foram compiladas para um futuro decreto regulamentador. Novamente procedeu-se  apresentao e discusso das alteraes com as entidades profissionais e empresariais parceiras (ASBEA, SECOVI, SINDUSCON, etc.).</p>
	<p>Dentre as principais alteraes e inovaes incorporadas  minuta do Projeto de Lei destacam-se:</p>
	<ul class="lista">
		<li>Previso de regras especficas para requalificaes de edificaes existentes;</li>
		<li>Reduo de exigncias tcnicas j disciplinadas por Normas Tcnicas;</li>
		<li>Previso de licenciamento com base em projeto simplificado, cabendo  anlise do rgo licenciador a verificao da compatibilidade do projeto proposto com as normas urbansticas e ambientais da cidade, incluindo melhoramentos virios, reas de preservao permanente, manejo arbreo, bem como segurana de uso, acessibilidade e condies de aerao da edificao; </li>
		<li>Responsabilizao de empreendedores e profissionais envolvidos no projeto e na obra, sendo o proprietrio e o possuidor do imvel, juntamente com o responsvel tcnico pelo projeto e com responsvel tcnico pela obra, solidariamente responsveis pelo correto atendimento  legislao municipal, normas tcnicas oficiais pertinentes e correta execuo da obra.</li>
		<li>No considerao, para efeito do licenciamento de obras, dos planos de melhoramento virio anteriores a 1988 no executados e sem decreto de utilidade pblica em vigor;</li>
		<li>Considerao do traado real dos melhoramentos virios j executados para efeito do licenciamento de obras;</li>
		<li>Eliminao da obrigatoriedade de licenciamento de atividades de baixo impacto urbanstico, tais como: obras complementares at 30 m, muros, alterao interna de unidade autnoma, reparo e limpeza, restauro de bem tombado com licena  especfica do rgo de preservao;</li>
		<li>Restrio da figura do projeto modificativo a pequenas alteraes em relao ao projeto j licenciado, em conformidade com o que foi estabelecido pelo PDE e LPUOS;</li>
		<li>Consolidao do cadastro de edificaes regulares com base nos dados de regularidade existentes;</li>
		<li>Diminuio do nmero de instncias de recurso visando abreviar as decises;</li>
		<li>Definio dos valores das taxas de anlise de forma progressiva em funo da complexidade dos projetos e da participao de maior nmero de tcnicos e secretarias na deciso dos grandes projetos, bem como iseno para obras residenciais de pequeno porte, habitao de interesse social e obras da administrao pblica direta e indireta.</li>
	</ul>
	<p>Aps a sano da Lei 16.642/2017, o Grupo de Trabalho da SMUL elaborou o Decreto regulamentador n 57.776/17 no prazo de 60 dias, com a colaborao das mesmas entidades que haviam participado da elaborao da Lei.</p>
	<p>O resultado deste processo pode ser conferido a seguir. Na primeira parte, com o objetivo de facilitar a compreenso, o leitor encontra o texto da lei combinado com o Decreto Regulamentador n 57.776/17. Na sequncia, a Portaria n 221 indica a documentao necessria e os padres de apresentao dos projetos. Na parte final, h o ndice remissivo, no qual esto listados os principais termos que compem o texto da lei e o decreto, permitindo que o leitor realize a busca por assuntos especficos.</p>
	<p style="text-align: right; margin-top: 2em;"><strong>Pedro Luiz Ferreira da Fonseca</strong></p>

