<template>
	<div class="Vetos">

		<p class="corpo_lei"><strong class="subtitulo_txt">Art. 15, Pargrafo nico, V</strong> - Instalao de estande de vendas.</p>
		<p class="veto">Razo do veto: Por esse dispositivo, o Alvar de Aprovao dever incluir, quando necessrio  implantao do projeto, as informaes relativas  previso da instalao de estande de vendas.</p>
		<p class="veto">Assim, da forma em que redigido o mencionado inciso, o Alvar de Aprovao deveria ser expedido previamente ao Alvar de Autorizao para a instalao do estande, causando inverso nos procedimentos atualmente praticados, de forma gil e eficiente, para a emisso dos documentos de controle da atividade edilcia.</p>
		<p class="veto">De fato, desde a edio do Decreto n 55.036, de 15 de abril de 2014, a autorizao para a instalao de estande de vendas pode ser obtida, por via eletrnica, mediante a simples identificao do nmero do processo relativo ao pedido de Alvar de Aprovao da obra, possibilitando que, antes de sua expedio, seja possvel a montagem do estande.</p>
		<p class="corpo_lei"><strong class="subtitulo_txt">Art. 23, 2 V</strong> - Instalao de canteiro de obras; <strong class="subtitulo_txt">VI</strong> - Instalao de tapume; <strong class="subtitulo_txt">VII</strong> - Transporte de terra ou entulho; <strong class="subtitulo_txt">VIII</strong> - Avano de grua sobre o espao pblico.</p>
		<p class="veto">Razo do veto: De acordo com esses dispositivos, o Alvar de Execuo incluir, quando for o caso, as licenas para a instalao de canteiro de obras, a instalao de tapume, o transporte de terra ou entulho e o avano de grua sobre o espao pblico.</p>
		<p class="veto">Entretanto, enquanto o Alvar de Execuo deve ser expedido antes do incio da obra, as licenas relativas s demais atividades edilcias supracitadas so emitidas, por via eletrnica, em situaes distintas, podendo ser no incio da obra, quando ela interferir no alinhamento virio, em suas vrias etapas ou, ainda, no momento da execuo de superestrutura. </p>
		<p class="veto">Ademais, considerando que essas licenas tm prazo de validade de 6 meses ao passo que o prazo do Alvar de Execuo  de 2 anos, o licenciamento simultneo implicaria a necessidade de sua revalidao no decorrer da execuo de toda a obra.</p>
		<p class="corpo_lei"><strong class="subtitulo_txt">Art. 29,  3</strong> O prazo de validade do alvar de execuo pode ser renovado aps a aprovao do projeto modificativo.</p>
		<p class="veto">Razo do veto: Esse pargrafo, por permitir a renovao do prazo de validade do Alvar de Execuo a cada aprovao de um novo projeto modificativo, poderia gerar a protelao desse prazo de forma indefinida, incentivando a apresentao desses projetos contrariamente ao escopo do artigo 27 do texto, que reduziu as hipteses de sua admisso a pequenos ajustes no projeto original.</p>
		<p class="veto">O  2 do artigo 29, alis, j impe, durante a anlise do projeto modificativo, a suspenso da contagem do prazo de validade do alvar, afigurando-se, assim, a sua renovao no somente desnecessria como tambm excessiva.</p>
		<p class="veto">Por outro lado, o benefcio alcanaria unicamente os interessados que modificarem os seus projetos e que, assim, teriam direito  renovao do prazo de validade do alvar, enquanto os demais deveriam observar  risca os prazos constantes do caput do artigo 29.</p>
		<p class="corpo_lei"><strong class="subtitulo_txt">Art. 108, IX</strong> - No estabelecimento de ensino universitrio mantido por instituio sem fins lucrativos, as reas destinadas ao atendimento  populao, com servios pblicos essenciais e gratuitos, no ser computada no clculo do coeficiente de aproveitamento e da taxa de ocupao, at o limite estabelecido na LPUOS.</p>
		<p class="veto">Razo do veto: Segundo essa norma, nos estabelecimentos de ensino universitrio mantidos por instituies sem fins lucrativos, as reas destinadas ao atendimento da populao, com servios pblicos essenciais e gratuitos, no seriam computadas no clculo do coeficiente de aproveitamento e da taxa de ocupao at o limite estabelecido na LPUOS  Lei n 16.402, de 22 de maro de 2016.</p>
		<p class="veto">Contudo, a referida Lei de Parcelamento, Uso e Ocupao do Solo j prev, no Captulo III do Ttulo V, a concesso de diversos incentivos para os denominados Usos Incentivados, dentre os quais se inserem os estabelecimentos de ensino, fixando, para eles, parmetros mais favorveis.</p>
		<p class="veto">Ademais, no se vislumbra razo lgica para a aplicao do benefcio apenas para os estabelecimentos de ensino universitrio, com a excluso daqueles voltados a outros nveis de ensino, medida a conflitar com o princpio da isonomia.</p>
		<p class="veto">A isso se acresa a dificuldade de determinar, nos projetos, as reas efetivamente utilizadas para o indigitado atendimento e tambm de fiscalizar a sua real existncia, alm da possibilidade da fcil alterao da destinao da rea retratada no projeto como de atendimento  populao.</p>
		<p class="corpo_lei"><strong class="subtitulo_txt">Anexo I, 5.6 </strong>No caso das edificaes residenciais que se beneficiarem de aumento de altura mxima, nos termos do art. 91 da Lei n 16.402, de 22 de maro de 2016, o afastamento contnuo dever ser de no mnimo 3,00 m de todas as divisas com lotes inseridos em Zona Exclusivamente Residencial, assim definidos na referida lei.</p>
		<p class="veto">Razo do veto: Estabelece esse subitem, no caso de edificaes residenciais beneficiadas com aumento de altura mxima nos termos do artigo 91 da LPUOS, um afastamento contnuo de, no mnimo, 3,00m de todas as divisas com lotes inseridos em Zona Exclusivamente Residencial.</p>
		<p class="veto">Entretanto, a matria  aumento do gabarito de altura mxima nas zonas de uso ZCOR-2, ZCOR-3 e ZCORa  est devidamente disciplinada nos artigos 59 e 91 da aludida Lei de Parcelamento, Uso e Ocupao do Solo, que preveem limitaes especficas para determinados loteamentos e imveis, permitindo a aplicao de regras prprias para cada caso.</p>
		<p class="veto">Com efeito, com o intuito de afastar ocupaes indesejadas de acordo com a situao concreta, o artigo 59 impe, para o aumento da altura mxima nessas zonas, o atendimento das restries convencionais de loteamentos, inclusive das concernentes aos recuos das edificaes, quando mais restritivas do que as fixadas pelas normas do texto aprovado e, de sua vez, o artigo 91 exige a anuncia expressa de todos os proprietrios limtrofes do imvel.</p>
		<p class="veto">O dispositivo em foco, ao contrrio, mostra-se inadequado na medida em que obriga, de forma indiscriminada, ao afastamento contnuo mnimo de 3,00m de todas as divisas com lotes, no podendo prevalecer.</p>
		<p class="corpo_lei"><strong class="subtitulo_txt">Anexo II, Tabela de taxas para exame e verificao dos pedidos de documentos de controle da atividade edilcia (R$)</strong></p>

		<table class="quadro" style="margin-top:15px;">
			<thead>
				<tr>
					<td rowspan="2">DOCUMENTO</td>
					<td rowspan="2">TIPIFICAO</td>
					<td rowspan="2">UNIDADE</td>
					<td rowspan="2">RESIDNCIA UNIFAMIIAR</td>
					<td colspan="3">OUTROS USOS (REA CONSTRUDA)</td>
				</tr>
				<tr>
					<td>at  1.500m</td>
					<td>de 1.500 a 20.000m</td>
					<td>acima de 20.000m</td>
				</tr>
			</thead>
			<tbody>
				<tr>
					<td rowspan="3">Revalidao de Alvar de Aprovao e de Alvar de Execuo</td>
					<td>Edificao nova</td>
					<td>unidade</td>
					<td>350,00</td>
					<td>720,00</td>
					<td>720,00</td>
					<td>720,00</td>
				</tr>
				<tr>
					<td>Reforma</td>
					<td>unidade</td>
					<td>350,00</td>
					<td>720,00</td>
					<td>720,00</td>
					<td>720,00</td>
				</tr>
				<tr>
					<td>Requalificao</td>
					<td>unidade</td>
					<td>350,00</td>
					<td>720,00</td>
					<td>720,00</td>
					<td>720,00</td>
				</tr>
			</tbody>
		</table>

		<p class="veto">Razo do veto: As aludidas linhas fixam as taxas para a revalidao do Alvar de Aprovao, podendo ensejar duplicidade de sua cobrana uma vez que essas taxas tambm constam das linhas referentes ao prprio Alvar de Aprovao.</p>

	</div>
</template>

<script>
export default{
  name: "Vetos", 
  data(){
    return{
      title: "Vetos"
    }
  }
}
</script>

