		<p class="corpo_portaria"><strong class="subtitulo_txt">3.F.2. </strong>Memorial descritivo das obras a serem executadas;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.F.3. </strong>Cronograma das obras a serem executadas.</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.F.4. </strong>Comprovao, nos sistemas da Prefeitura, do cadastro de equipamento mecnico de transporte permanente, tais como elevador, escada rolante e plataforma de elevao.</p>
		<h2>Seo 3.G - Certificado de Segurana</h2>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.1. </strong>Peas grficas do projeto simplificado, contendo indicao de todos os equipamentos que compem o sistema de segurana proposto, assinadas por profissional habilitado.</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.1.1. </strong>Quando no existirem obras de adaptao a serem executadas, o projeto simplificado do certificado de segurana pode ser substitudo pelo projeto aprovado do Corpo de Bombeiros.</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.2. </strong>Memorial descritivo das obras a serem executadas;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.3. </strong>Certificado de Acessibilidade ou o protocolo do pedido;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.4. </strong>Declarao assinada pelo profissional habilitado atestando que a edificao atende s disposies do COE e legislao complementar, quanto s condies de segurana de uso, quando no for necessrio a execuo de obras e servios de adaptao;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.5. </strong>Outras declaraes para comprovao das condies de segurana de uso exigidas na legislao municipal.</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.G.6. </strong>Laudo Tcnico de Segurana devidamente preenchido, assinado pelo proprietrio e/ou responsvel pelo uso da edificao e por profissional habilitado, conforme modelo a ser estabelecido em Portaria.</p>
		<h2>Seo 3.H - Alvar de Autorizao</h2>

		<h4 class="subsecao_portaria">Subseo 3.H.I - Avano de tapume sobre parte do passeio pblico</h4>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.I.1. </strong>Devero ser apresentadas declaraes assinadas pelo proprietrio ou possuidor do imvel e responsvel tcnico pela obra conforme os itens a seguir:</p>
		<ol class="lista_a">
			<li>Estar ciente dos direitos e responsabilidades expressos na Seo I do Captulo II da Lei n 16.642, de 09 de maio de 2017 (Cdigo de Obras e Edificaes), atribudos ao Municpio, ao proprietrio ou possuidor de imveis e aos profissionais atuantes em projeto e construo, observadas as disposies da Legislao de Obras e Edificaes.</li>
			<li>Que este Alvar de Autorizao permite apenas, e a ttulo precrio, a execuo das obras ou servios nele descritos, e que poder ser cancelado a qualquer tempo quando constatado desvirtuamento do seu objeto inicial, ou quando a Prefeitura no tiver interesse na sua manuteno ou renovao;</li>
			<li>Que o incio de obras observou os prazos previstos no artigo 71 da Lei n 16.642/17 e artigo 59 do Decreto n 57.776/17;</li>
			<li>Que na execuo de servio/obra no alinhamento at a altura de 4,00 m em relao ao passeio pblico, dever ser instalado tapume que poder avanar sobre o passeio, no mximo, a metade de sua largura. Concludo o servio de fachada ou paralisada a obra por perodo superior a 30 (trinta) dias, o tapume deve ser obrigatoriamente recuado para o alinhamento;</li>
			<li>Que no caso de plataforma de segurana, o avano sobre o passeio fica limitado  largura do passeio pblico, respeitadas eventuais interferncias com equipamentos urbanos e/ou vegetao de porte arbreo, observado o p direito mnimo de 2,50 m. </li>
		</ol>

		<h4 class="subsecao_portaria">Subseo 3.H.II - Avano de grua sobre o espao pblico</h4>

		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.II.1. </strong>Declaraes assinadas pelo responsvel tcnico pela obra, sob penas da lei, e de acordo com as competncias conferidas pelo CONFEA/CREA/CAU conforme os itens a seguir:</p>
		<ol class="lista_a">
			<li>De ser o tcnico habilitado registrado junto ao rgo federal fiscalizador do exerccio profissional, podendo atuar como pessoa fsica ou como responsvel por pessoa jurdica, respeitadas as atribuies e limitaes consignadas por aquele organismo.</li>
			<li>Estar ciente dos direitos e responsabilidades expressos na Seo I do Captulo II da Lei n 16.642, de 09 de maio de 2017 (Cdigo de Obras e Edificaes) atribudos ao Municpio, ao proprietrio ou possuidor de imveis e aos profissionais atuantes em projeto e construo, observadas as disposies da Legislao de Obras e Edificaes.</li>
			<li>Que as instalaes de gruas devero observar o gabarito estabelecido pelo rgo Regional do DECEA e quando ultrapassarem o seu limite, devero ser submetidas  nova deliberao deste rgo.</li>
			<li>Pelo total atendimento s Normas Tcnicas Oficiais, em especial,  NBR 7678, inclusive quanto  construo de cobertura de proteo sobre o passeio, e aos demais preceitos legais e normativos contidos na legislao municipal e  NBR 8400, que dispe sobre o clculo de equipamento para levantamento e movimentao de cargas;</li>
			<li>Que a base da grua est posicionada no terreno dentro dos recuos previstos em lei e que, alm da ponta da lana sem carga, nenhuma outra parte do equipamento (cargas iadas, contrapeso, etc.) se projeta para alm dos limites do terreno;</li>
			<li>Que a ponta da lana da grua estar afastada no mnimo 3 m (trs metros) de qualquer obstculo e se projetar no mximo 10 m (dez metros) alm do alinhamento do lote;</li>
			<li>Que, quando houver necessidade de fechamento total ou parcial da via pblica, para atendimento  NBR 7678, ser providenciada a respectiva autorizao prvia da Companhia de Engenharia de Trfego (CET);</li>
			<li>O compromisso de desmontagem imediata do equipamento em caso de paralisao da obra;</li>
			<li>Que o incio de obras observou os prazos previstos no artigo 71 da Lei n 16.642/17 e artigo 59 do Decreto n 57.776/17.</li>
		</ol>
		<h4 class="subsecao_portaria">Subseo 3.H.III - Instalao de canteiro de obras em imvel distinto daquele em que a obra ser executada </h4>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.III.1. </strong>Cpia do Alvar de Execuo, no caso de obra particular;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.III.2. </strong>Cpia do contrato firmado pelo rgo ou entidade pblica no qual as obras ou servios esto afetos, no caso de obra pblica;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.III.3. </strong>Croqui de localizao, em duas vias;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.III.4. </strong>Croqui do canteiro de obras, em duas vias;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.III.5. </strong>Cronograma de execuo de obra, em duas vias;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.III.6. </strong>Declarao de termo de responsabilidade no atendimento s normas contidas no COE e do decreto regulamentador, assinado pelo profissional responsvel pela instalao e utilizao do canteiro de obras, em duas vias;</p>

		<h4 class="subsecao_portaria">Subseo 3.H.IV - Estande de vendas no mesmo local de implantao da obra ou em imvel distinto daquele em que a obra ser executada</h4>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.H.IV.1. </strong>Devero ser apresentadas declaraes assinadas pelo proprietrio ou possuidor do imvel, responsvel tcnico pela obra conforme os itens a seguir:</p>
		<ol class="lista_a">
			<li>Estar ciente dos direitos e responsabilidades expressos na Seo I do Captulo II da Lei n 16.642, de 09 de maio de 2017 (Cdigo de Obras e Edificaes), atribudos ao Municpio, ao proprietrio ou possuidor de imveis e aos profissionais atuantes em projeto e construo, observadas as disposies da Legislao de Obras e Edificaes.</li>
			<li>Que o projeto do Estande de Vendas est em conformidade s disposies do COE e legislao correlata.</li>
			<li>Que este Alvar de Autorizao permite apenas, e a ttulo precrio, a execuo das obras ou servios nele descritos, e que poder ser cancelado a qualquer tempo quando constatado desvirtuamento do seu objeto inicial, ou quando a Prefeitura no tiver interesse na sua manuteno ou renovao;</li>
			<li>Que na existncia de obras situadas no alinhamento ou dele afastadas at 1,20 m antes de seu incio dever ser solicitado a Alvar de Autorizao para Avano de Tapume Sobre Parte do Passeio Pblico.</li>
			<li>Que no efetivar manejo arbreo para a implantao do estande de vendas.</li>
		</ol>
		<h2>Seo 3.I - Cadastro de Equipamentos</h2>
		<h4 class="subsecao_portaria">Subseo 3.I.I - Equipamento mecnico de transporte permanente deve ser instrudo apenas com os dados tcnicos do aparelho e do imvel no qual est instalado, que se dar por meio do licenciamento eletrnico.</h4>
		<h4 class="subsecao_portaria">Subseo 3.I.II - Tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins devem ser instrudos com:</h4>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.II.1. </strong>Peas grficas com indicao e localizao de cada equipamento, assinada por profissional habilitado, contendo:</p>
		<ol class="lista_a">
			<li>Planta com a indicao de todos os equipamentos instalados, assim como a representao dos itens de segurana e das edificaes;</li>
			<li>Corte da edificao e dos equipamentos instalados.</li>
		</ol>

		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.II.3. </strong>Memorial descritivo com a localizao, qualificao, quantificao e descrio de cada equipamento, assinado por profissional habilitado;</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.II.4. </strong>Declarao assinada por profissional habilitado atestando que o equipamento foi instalado conforme projeto e que atende s normas tcnicas aplicveis e s disposies da legislao municipal na data do protocolo; </p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.II.5. </strong>Declaraes especficas assinadas por profissional habilitado a respeito das condies de funcionamento dos equipamentos e segurana da edificao.</p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.II.6. </strong>Declarao assinada pelo responsvel tcnico atestando que as edificaes correspondem ao fiel existente no local e projeto de edificao;</p>

		<h4 class="subsecao_portaria">Subseo 3.I.III - Equipamento de Sistema Especial de Segurana da edificao deve ser instrudo com:</h4>

		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.III.1. </strong>Peas grficas com indicao e localizao de cada equipamento, assinada por profissional habilitado, contendo:</p>
		<ol class="lista_a">
			<li>Implantao da edificao em conformidade com o projeto aprovado;</li>
			<li>Planta baixa de todos os pavimentos da edificao, indicando os equipamentos instalados;</li>
			<li>Corte da edificao contendo os equipamentos instalados;</li>
		</ol>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.III.2. </strong>Memorial descritivo com informaes dos equipamentos do sistema de segurana; </p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.III.3. </strong>Declarao assinada por profissional habilitado atestando que os equipamentos foram instalados conforme projeto e que atendem s normas tcnicas aplicveis e s disposies da legislao municipal em conformidade com os documentos exigidos. </p>
		<p class="corpo_portaria"><strong class="subtitulo_txt">3.I.III.4. </strong>Declaraes especficas assinadas por profissional habilitado a respeito das condies de funcionamento dos equipamentos e segurana da edificao.</p>

		<h2>Seo 3.J - Manuteno de Equipamentos</h2>

		<h4 class="subsecao_portaria">Subseo 3.J.I - Equipamento mecnico de transporte permanente j instalado e que permanea sem modificao na sua caracterstica dever renovar o cadastro de equipamento por meio da emisso do Relatrio de Inspeo Anual (RIA), previsto em legislao especifica;</h4>

		<h4 class="subsecao_portaria">Subseo 3.J.II - Tanque de armazenagem, bomba, filtro de combustvel e equipamentos afins devero ser instrudos com:</h4>
		<ol class="lista_a">
			<li>Declarao assinada pelo responsvel tcnico e proprietrio ou possuidor do imvel pelo controle da segurana da edificao, atestando que os equipamentos, condies edilcias, de uso e segurana contra incndio permanecem conforme o cadastro aprovado, que possuem manuteno peridica e atendem s normas tcnicas aplicveis e s disposies da legislao municipal; </li>
			<li>Declaraes especficas assinadas por profissional habilitado a respeito das condies de funcionamento dos equipamentos e segurana da edificao. </li>
		</ol>
		<h4 class="subsecao_portaria">Subseo 3.J.III - Equipamento de Sistema Especial de Segurana da edificao deve ser instrudo com:</h4>
		<ol class="lista_a">
			<li>Declarao assinada pelo responsvel tcnico e proprietrio ou possuidor do imvel pelo controle da segurana da edificao, atestando que os equipamentos, condies edilcias, de uso e segurana contra incndio permanecem conforme o cadastro aprovado, que possuem manuteno peridica e atendem s normas tcnicas aplicveis e s disposies da legislao municipal;</li>
			<li>Declaraes especficas assinadas por profissional habilitado a respeito das condies de funcionamento dos equipamentos e segurana da edificao. </li>
		</ol>
